import Install from '@container/install/Install';
import Order from '@container/order/Order';

const routes = [
    {
        name: 'Main',
        path: '/',
        element: process.env.REACT_APP_MAIN_PATH === 'Order' ? <Order /> : <Install />,
    },
    {
        name: 'Install',
        path: '/Install',
        element: <Install />,
    },
    {
        name: 'Order',
        path: '/Order',
        element: <Order />,
    },
];

export default routes;
