import classStyle from './Order.module.scss';
import logo from '@src/assets/logo.png';
// import usdtLogo from '@src/assets/usdtLogo.png';
import appLogo from '@src/assets/appLogo.png';
import scan from '@src/assets/scan.png';
import info3 from '@src/assets/info3.png';
import info4 from '@src/assets/info4.png';
import copy from '@src/assets/copy.png';
import { useEffect, useState, useContext } from 'react';
import classNames from 'classnames';
import { useWindowDimensions } from '@src/hooks';
import copyTo from 'copy-to-clipboard';
import Services from '@src/api/services';
import router from '@src/routes/router';
import { downloadUrl } from '@src/baseUrl';
import moment from 'moment';
import { isMobile } from '@src/unit';
import { QRCode } from 'react-qrcode-logo';
import { ToastContext } from '@src/context';

const maxWidth = 1200;

export default function Order(props) {
    const toastContext = useContext(ToastContext);
    const { width } = useWindowDimensions();
    const [isLoading, setIsLoading] = useState(true);
    const [coinType, setCoinType] = useState('');
    const [list, setList] = useState({
        apiOrderId: {
            key: 'apiOrderId',
            title: 'ID',
            value: '',
        },
        amount: {
            key: 'amount',
            title: '',
            value: '',
        },
        validTime: {
            key: 'validTime',
            title: '',
            value: '',
        },
    });
    const [chainInfoList, setChainInfoList] = useState([]);
    const isMobileType = isMobile || width <= 768;
    const changeSecondsToHours = (value) => {
        const time = moment.duration(value, 'seconds');
        // ex 100
        let hours = parseInt(time.asHours());

        if (hours < 10) {
            hours = `0${hours}`;
        }

        const minutes = time.minutes();
        const seconds = time.seconds();
        const mmss = moment({ m: minutes, s: seconds }).format('mm:ss');

        return `${hours}:${mmss}`;
    };

    useEffect(() => {
        checkInstalledRelatedApps();
        return;
        const { token } = router.getQuery();
        let interval;

        if (!token) {
            toastContext.setToast('', 1.5 * 1000 * 100);

            return;
        }

        Services.getCharge({ token })
            .then((res) => {
                if (res.data.success) {
                    const { coinType, apiOrderId, amount, validTime, thirdlyChargeChainInfoList } =
                        res.data.data;
                    let validTimeCount = validTime;
                    list.apiOrderId.value = apiOrderId;
                    list.amount.value = parseFloat(amount);
                    list.validTime.value = changeSecondsToHours(validTimeCount);

                    setCoinType(coinType);
                    setList({ ...list });
                    setChainInfoList(thirdlyChargeChainInfoList);
                    setIsLoading(false);

                    interval = setInterval(() => {
                        validTimeCount--;

                        if (validTimeCount >= 0) {
                            list.validTime.value = changeSecondsToHours(validTimeCount);
                            setList({ ...list });
                        } else {
                            clearInterval(interval);
                        }
                    }, 1000);
                } else {
                    if (res.data.msg) {
                        toastContext.setToast(res.data.msg, 1.5 * 1000 * 100);
                    } else {
                        toastContext.setToast('', 1.5 * 1000 * 100);
                    }
                }
            })
            .catch((err) => {
                console.log('kailog', err);
                toastContext.setToast('', 1.5 * 1000 * 100);
            });

        return () => {
            if (interval) {
                clearInterval(interval);
            }
        };
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const checkInstalledRelatedApps = async () => {
        try {
            const installedApps = await navigator.getInstalledRelatedApps();

            const appid = 'com.example.rp';
            const nativeApp = installedApps.find((app) => app.id === appid);
            console.log('kailog', installedApps);

            if (nativeApp) {
                console.log('kailog', nativeApp);
                toastContext.setToast(appid, 1.5 * 1000 * 100);
                window.open('ubitrp://open', '_blank');
            } else {
                toastContext.setToast('nativeApp err', 1.5 * 1000 * 100);
            }
        } catch (error) {
            alert(error);
        }
    };

    if (isLoading) {
        return <div className={classStyle.loading}></div>;
    }

    // pc mobile
    const pcList = [list.apiOrderId, list.amount, list.validTime];
    const mobileList = [list.validTime, list.apiOrderId, list.amount];
    const fromList = isMobileType ? mobileList : pcList;

    return (
        <div className={classStyle.page}>
            <img src={logo} className={classStyle.logo} alt="" />
            <div className={classStyle.main}>
                <div className={classStyle.from}>
                    <div className={classStyle.formTitle}></div>
                    <div className={classStyle.list}>
                        <FromItem item={fromList[0]} coinType={coinType} />
                        <div className={classStyle.fromItemBar}>
                            <FromItem item={fromList[1]} coinType={coinType} />
                            <FromItem item={fromList[2]} coinType={coinType} />
                        </div>
                    </div>
                </div>
                <QRCardView
                    width={width}
                    isMobileType={isMobileType}
                    chainInfoList={chainInfoList}
                    coinType={coinType}
                />
                <UseList width={width} isMobileType={isMobileType} />
            </div>
        </div>
    );
}

const FromItem = ({ item, coinType }) => {
    const toastContext = useContext(ToastContext);
    const { key, title, value } = item;
    const isId = key === 'apiOrderId';

    return (
        <div className={classStyle.item}>
            <div className={classStyle.itemTitle}>{title}</div>
            <div className={isId ? classStyle.itemValueId : classStyle.itemValue}>{value}</div>
            {isId && value !== '' && (
                <img
                    className={classStyle.copy}
                    src={copy}
                    alt=""
                    onClick={() => {
                        copyTo(value);
                        toastContext.setToast('', 1500);
                    }}
                />
            )}
            {key === 'amount' && value !== '' && (
                <div className={classStyle.itemAmount}>{coinType}</div>
            )}
        </div>
    );
};

const QRCardView = ({ width, isMobileType, chainInfoList, coinType }) => {
    const size = isMobileType ? (width / 750) * 250 : (width / maxWidth) * 250;
    const [chainIndex, setIndex] = useState(0);
    const chainInfo = chainInfoList[chainIndex];
    const renderQrButton = (item, index) => {
        const isActive = index === chainIndex;
        const text = `${item.chainName}`;

        return (
            <div
                key={index}
                className={classNames(classStyle.button, isActive && classStyle.isActive)}
                onClick={() => setIndex(index)}
            >
                {text}
            </div>
        );
    };

    const qrTexts = [
        'UBIT',
        'ubit',
        ' ',
    ];

    return (
        <div className={classStyle.qrCardView}>
            {coinType === 'USDT' && (
                <div className={classStyle.buttons}>{chainInfoList.map(renderQrButton)}</div>
            )}
            <div className={classStyle.qrCodeTitle}></div>
            <div className={classStyle.qrCardInfo}>
                <QRCode
                    value={chainInfo.qrcodeContent}
                    size={size}
                    quietZone={10}
                    ecLevel="M"
                    // logoImage={usdtLogo}
                    qrStyle="dots"
                />
                <div className={classStyle.qrTexts}>
                    <div></div>
                    {qrTexts.map((item, index) => {
                        const num = `${index + 1}.`;

                        return (
                            <div key={index} className={classStyle.qrTextItem}>
                                <div className={classStyle.qrTextNum}>{num}</div>
                                <div>{item}</div>
                            </div>
                        );
                    })}
                </div>
            </div>
        </div>
    );
};

const UseList = ({ width, isMobileType }) => {
    const list = [
        {
            title: ' UBIT',
            text: '(UBIT  UBIT)',
            img: appLogo,
            imgClass: classStyle.appLogo,
            showQrCode: true,
        },
        {
            title: ' ',
            text: '',
            img: undefined,
            showQrCode: false,
        },
        {
            title: ` `,
            title2: `  `,
            text: '',
            img: info3,
            imgClass: classStyle.infoImg,
            showQrCode: false,
        },
        {
            title: ' ',
            text: '- -',
            img: info4,
            imgClass: classStyle.infoImg,
            showQrCode: false,
        },
        {
            title: '  ,',
            text: '',
            img: undefined,
            showQrCode: false,
        },
    ];

    const size = isMobileType ? (width / 750) * 118 : (width / maxWidth) * 118;

    return (
        <div className={classStyle.useList}>
            <div className={classStyle.useListTitle}></div>
            {list.map((item, index) => {
                const { title, title2, text, img, imgClass, showQrCode } = item;

                return (
                    <div key={index} className={classStyle.useListItem}>
                        <div className={classStyle.useItemTitleBar}>
                            <span className={classStyle.useItemTitle}>{title}</span>
                            {title2 && (
                                <>
                                    <img className={classStyle.scan} src={scan} alt="" />
                                    <span className={classStyle.useItemTitle}>{title2}</span>
                                </>
                            )}
                        </div>
                        <div className={classStyle.useItemText}>{text}</div>
                        <div className={classStyle.useItemInfo}>
                            {img && (
                                <img
                                    className={imgClass}
                                    src={img}
                                    alt=""
                                    onClick={() => {
                                        window.location.href = downloadUrl;
                                    }}
                                />
                            )}
                            {showQrCode && (
                                <>
                                    <div className={classStyle.useItemInfoText}></div>
                                    {showQrCode && (
                                        <QRCode value={downloadUrl} size={size} quietZone={10} />
                                    )}
                                </>
                            )}
                        </div>
                    </div>
                );
            })}
        </div>
    );
};
