import React, { useState } from 'react';
import './App.scss';
import routes from '@src/routes';
import { unstable_HistoryRouter as HistoryRouter, Route, Routes } from 'react-router-dom';
import history from '@src/routes/history';
import Toast from '@src/container/common/Toast.js';
import { ToastContext } from '@src/context';

function App() {
    const [toastText, setToastText] = useState(null);
    const handleSetToast = (text, closeTime = 1500) => {
        setToastText(text);
        setTimeout(() => {
            setToastText(null);
        }, closeTime);
    };

    return (
        <div className="App">
            <ToastContext.Provider
                value={{
                    setToast: handleSetToast,
                }}
            >
                {toastText && <Toast text={toastText} />}
                <HistoryRouter history={history}>
                    <Routes>{routes.map(renderRoute)}</Routes>
                </HistoryRouter>
            </ToastContext.Provider>
        </div>
    );
}

const renderRoute = (route) => {
    const { name, path, element, exact = true } = route;

    return <Route key={name} path={path} exact={exact} element={element} />;
};

export default App;
