import classStyle from './Install.module.scss';
import { useState } from 'react';
import logo from '@src/assets/logo.png';
import bgLogo from '@src/assets/bgLogo.png';
import step1 from '@src/assets/step1.png';
import step2 from '@src/assets/step2.png';
import step3 from '@src/assets/step3.png';
import step4 from '@src/assets/step4.png';
import step5 from '@src/assets/step5.png';
import pcBg from '@src/assets/pcBg.png';
import pcLogo from '@src/assets/pcLogo.png';
import { QRCode } from 'react-qrcode-logo';
import { useWindowDimensions } from '@src/hooks';
import { downloadUrl, androidInstall, iosInstall, toSettingUrl } from '@src/baseUrl';
import { isMobile, isAndroid } from '@src/unit';

const list = [
    {
        title: ' , ,',
        img: step1,
    },
    {
        title: ' , ',
        img: step2,
    },
    {
        title: ' ',
        img: step3,
    },
    {
        title: '  VPN',
        img: step4,
    },
    {
        title: ' ,  , ',
        img: step5,
    },
];

export default function Install() {
    const { width } = useWindowDimensions();

    if (isMobile || width <= 768) {
        return <MobileInstall />;
    } else {
        return <PcInstall width={width} />;
    }
}

const MobileInstall = () => {
    const [showIframe, setShowIframe] = useState(false);
    const [showIframe2, setShowIframe2] = useState(false);
    const handleDownloadShow = () => {
        setShowIframe(true);
        setTimeout(() => {
            setShowIframe2(true);
        }, 1000);
    };
    const handleDownload = () => {
        if (isAndroid) {
            const link = androidInstall;

            window.location.href = link;
        } else {
            if (showIframe) {
                setShowIframe(false);
                setShowIframe2(false);
                setTimeout(() => {
                    handleDownloadShow();
                }, 1000);
            } else {
                handleDownloadShow();
            }
        }
    };

    return (
        <div className={classStyle.mobilePage}>
            <div className={classStyle.mainBg}>
                <img src={logo} className={classStyle.logo} alt="" />
                <img src={bgLogo} className={classStyle.bgLogo} alt="" />
                <div className={classStyle.logoText}> </div>
                <div className={classStyle.button} onClick={handleDownload}>
                    APP
                </div>
                <div className={classStyle.list}>
                    {list.map((item, index) => {
                        return (
                            <div className={classStyle.item} key={index}>
                                <div className={classStyle.itemTitle}>{item.title}</div>
                                <img className={classStyle.itemImg} src={item.img} alt="" />
                            </div>
                        );
                    })}
                </div>
            </div>
            {showIframe && (
                <iframe title="saveUrl" className={classStyle.iframe} src={iosInstall} />
            )}
            {showIframe2 && (
                <iframe title="toSettingUrl" className={classStyle.iframe} src={toSettingUrl} />
            )}
        </div>
    );
};

const PcInstall = ({ width }) => {
    const size = (width / 1200) * 150;

    return (
        <div className={classStyle.pcPage}>
            <img src={pcBg} className={classStyle.pcBg} alt="" />
            <div className={classStyle.mainBg}>
                <div className={classStyle.mainBgInfo}>
                    <div className={classStyle.mainBgInfoLeft}>
                        <img src={logo} className={classStyle.logo} alt="" />
                        <div className={classStyle.logoText}> </div>
                        <div className={classStyle.qrCode}>
                            <QRCode value={downloadUrl} size={size} />
                            <div className={classStyle.qrCodeText}></div>
                        </div>
                    </div>
                    <img src={pcLogo} className={classStyle.mainBgInfoRight} alt="" />
                </div>
                <div className={classStyle.list}>
                    {list.map((item, index) => {
                        return (
                            <div className={classStyle.item} key={index}>
                                <div className={classStyle.itemTitle}>{item.title}</div>
                                <img className={classStyle.itemImg} src={item.img} alt="" />
                            </div>
                        );
                    })}
                </div>
            </div>
        </div>
    );
};
